<template>
  <div class="star" :class="starType">
    <span v-for="itemClass in itemClasses" :class="itemClass" class="star-item">x</span>
  </div>
</template>

<script>
  /* eslint-disable space-infix-ops,keyword-spacing,semi-spacing,no-multi-spaces,comma-spacing */

  const CLS_ON='on'
  const CLS_HALF='half'
  const CLS_OFF='off'
  const LENGTH=5
  /* eslint-disable no-trailing-spaces,key-spacing,space-before-function-paren,space-before-blocks,space-infix-ops */

  export default {
    props:{
      size:{
        type:Number
      },
      score:{
        type:Number
      }

    },
    computed:{
      starType(){
        return 'star-'+this.size
      },
      itemClasses(){
        let result = []
        let score =Math.floor(this.score*2)/2
        let hasDecimal = score % 1 !==0
        let integer = Math.floor(score)
        for(let i =0;i<integer;i++){
          result.push(CLS_ON)
        }
        if(hasDecimal){
          result.push(CLS_HALF)
        }
        while(result.length<LENGTH){
          result.push(CLS_OFF)
        }
//        console.log(result,this.starType)
        return result
      }
    }
  }
</script>

<style  lang="stylus" rel="stylesheet/stylus">
  @import '../../common/style/mixin.styl'
    .star
      font-size :0
      .star-item
        display:inline-block
        background-repeat :no-repeat
      &.star-24
        .star-item
          width: 10px
          height: 10px
          margin-right:3px
          background-size:10px 10px
          &:last-chid
            margin-right:0px
          &.on
            bg-image('star24_on')
          &.half
            bg-image('star24_half')
          &.off
            bg-image('star24_off')
      &.star-36
        .star-item
          width: 15px
          height: 15px
          margin-right:6px
          background-size:15px 15px
          &:last-chid
            margin-right:0px
          &.on
            bg-image('star36_on')
          &.half
            bg-image('star36_half')
          &.off
            bg-image('star36_off')
      &.star-48
        .star-item
          width: 20px
          height: 20px
          margin-right:22px
          background-size:20px 20px
          &:last-chid
              margin-right:0px
          &.on
           bg-image('star48_on')
          &.half
           bg-image('star48_half')
          &.off
           bg-image('star48_off')


</style>



