<template>
   <div class="header">
       <div class="content-wrapper">
           <div class="avatar">
             <img :src="seller.avatar" width="64" height="64" alt="商家图片">
           </div>
           <div class="content">
               <div class="title">
                   <span class="brand"></span>
                   <span class="name">{{seller.name}}</span>
               </div>
               <div class="description">
                   {{seller.description}}/{{seller.deliveryTime}}分送达
               </div>

               <div v-if="seller.supports" class="support">
                     <span class="icon" :class="classMap[seller.supports[0].type]"></span>
                     <span class="text">{{seller.supports[0].description}}</span>
               </div>

           </div>
           <div class="support-count" v-if="seller.supports" @click="showDetail">
             <span class="count">{{seller.supports.length}}</span>
             <span class="icon-keyboard_arrow_right"></span>
           </div>
       </div>
       <div class="bulletin-wrapper" @click="showDetail">
            <span class="bulletin-title"></span>
            <span class="bulletin-text">{{seller.bulletin}}</span>
            <span class="icon-keyboard_arrow_right"></span>
       </div>
       <div class="background">
         <img :src="seller.avatar" alt="" width="100%" height="100%">
       </div>
       <transition name="fade">
         <div class="detail" v-show="detailShow" >
           <div class="detail-wrapper clearfix">
             <div class="main-detail">
               <h1 class="name">{{seller.name}}</h1>
               <div class="star-wrapper">
                 <star :size="48" :score="seller.score"></star>
               </div>
               <div class="title">
                 <div class="line"></div>
                 <div class="text">优惠信息</div>
                 <div class="line"></div>
               </div>
               <ul v-if="seller.supports" class="supports">
                 <li class="support-item" v-for="(item,index) in seller.supports">
                   <span class="icon" :class="classMap[seller.supports[index].type]"></span>
                   <span class="text">{{item.description}}</span>
                 </li>
               </ul>
               <div class="title">
                 <div class="line"></div>
                 <div class="text">商家公告</div>
                 <div class="line"></div>
               </div>
               <div class="bulletin">
                 <p class="content">{{seller.bulletin}}</p>
               </div>
             </div>
           </div>
           <div class="detail-close">
             <span class="icon-close" @click="closeDetail"></span>
           </div>
         </div>
       </transition>
   </div>
</template>

<script>
  /* eslint-disable no-trailing-spaces,key-spacing,space-before-function-paren,space-before-function-paren,space-before-blocks,space-infix-ops,comma-spacing,no-undef */
import star from '../star/star.vue'
  export default{
    props:{
      seller:{
        type:Object
      }
    },
    data(){
      return {
        detailShow:false
      }
    },
    methods:{
      showDetail(){
        this.detailShow=true
      },
      closeDetail(){
        this.detailShow=false
      }
    },
    created(){
      this.classMap=['decrease','discount','special','invoice','guarantee']
    },
    components:{
      star
    }

  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/style/mixin.styl"
   .header
     position :relative
     overflow :hidden
     color: #ffffff
     background-color:rgba(7,17,27,0.5)
     font-size:0
     .content-wrapper
       position:relative
       padding:24px 12px 18px 24px
       .avatar
         display: inline-block
         vertical-align:top
         img
           border-radius :2px
       .content
         display :inline-block
         font-size:14px
         margin-left:16px
         .title
           margin:2px 0 8px 0
           .brand
             display :inline-block
             vertical-align top
             width: 30px
             height:18px
             bg-image('brand')
             background-size:30px 18px
             background-repeat no-repeat
           .name
             margin-left:6px
             font-size:16px
             line-height :18px
             font-weight :bold
         .description
           margin-bottom:10px
           line-height 12px
           font-size :12px
         .support
             font-size :0px
            .icon
              display inline-block
              width:12px
              vertical-align: top
              height: 12px
              margin-right:4px
              background-size :12px 12px
              background-repeat:no-repeat
              &.decrease
                bg-image('decrease_1')
              &.discount
                bg-image('discount_1')
              &.guarantee
                bg-image('guarantee_1')
              &.invoice
               bg-image('invoice_1')
              &.special
               bg-image('special_1')


            .text
              font-size: 10px
              line-height :12px

       .support-count
        position:absolute
        right:12px
        bottom:14px
        padding: 0 8px
        height:24px
        line-height :24px
        border-radius :14px
        background:rgba(0,0,0,0.2)
        text-align :center
        font-size:0
        .count
          font-size: 10px
          vertical-align:top
        .icon-keyboard_arrow_right
          font-size:10px
          line-height: 24px
          margin-left:2px
     .bulletin-wrapper
       background:rgba(7,17,27,0.2)
       position:relative
       font-size :10px
       height: 28px
       line-height 28px
       padding:0 22px 0 12px
       white-space: nowrap
       overflow :hidden
       text-overflow :ellipsis
       .bulletin-title
         display :inline-block
         width: 22px
         height: 12px
         bg-image('bulletin')
         background-size:22px 12px
         background-repeat:no-repeat
         vertical-align :top
         margin-top:8px
       .bulletin-text
         vertical-align :top
         margin:0 4px
       .icon-keyboard_arrow_right
         position:absolute
         font-size: 10px
         right: 12px
         top:8px


     .background
       position:absolute
       top:0
       left:0
       height: 100%
       width:100%
       z-index :-1
       filter:blur(10px)
     .detail
       position:fixed
       top:0px
       left: 0px
       width:100%
       height:100%
       transition :all 0.5s
       overflow :auto
       z-index:101
       backdrop-filter:blur(10px)
       opacity :1
       background:rgba(7,17,27,0.8)
       &.fade-enter-active, &.fade-leave-active //vue的动画
         transition: all 0.5s
       &.fade-enter, &.fade-leave-active
         opacity: 0
         background: rgba(7, 17, 27, 0)
       .detail-wrapper
        min-height :100%
        width:100%
        .main-detail
          margin-top: 64px
          padding-bottom:64px
          .name
            font-size: 16px
            text-align :center
            line-height :16px
            font-weight :700
          .star-wrapper
            margin-top: 18px
            padding:2px 0
            text-align :center
          .supports
            width:80%
            margin:0 auto
            .support-item
              padding:0 12px
              margin-bottom:12px
              font-size:0
              &:last-chid
                margin-bottom:0
              .icon
                display :inline-block
                width: 16px
                height: 16px
                vertical-align :top
                margin-right: 6px
                background-size:16px 16px
                background-repeat :no-repeat
                &.decrease
                  bg-image('decrease_2')
                &.discount
                  bg-image('discount_2')
                &.guarantee
                  bg-image('guarantee_2')
                &.invoice
                  bg-image('invoice_2')
                &.special
                  bg-image('special_2')

              .text
                line-height :16px
                font-size :12px
          .title
            display :flex
            width:80%
            margin:28px auto 24px auto
            .line
              flex :1
              position:relative
              top: -6px
              border-bottom :1px solid rgba(255,255,255,0.2)
            .text
             padding:0 24px
             font-size:14px
             font-weight :700
          .bulletin
            width:80%
            margin:0 auto
            .content
              pading:0 12px
              line-height :24px
              font-size: 12px


       .detail-close
         position :relative
         height: 32px
         width: 32px
         margin:-64px auto 0 auto
         clear:both
         font-size :32px



</style>



