<template>
  <transition name="move">
  <div class="food" v-show="showFlag" ref="food">
     <div class="food-content">
       <div class="image-header">
         <img :src="food.image" alt="">
         <div class="back">
           <i class="icon-arrow_lift" @click="hide($event)"></i>
         </div>
       </div>
       <div class="content">
         <h1 class="title">{{food.name}}</h1>
         <div class="detail">
           <span class="sell-count">月售{{food.sellCount}}份</span>
           <span class="rating">好评率{{food.rating}}%</span>
         </div>
         <div class="price">
           <span class="now">￥{{food.price}}</span>
           <span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
         </div>
         <div class="cartcontrol-wrapper">
           <cartcontrol :food="food"></cartcontrol>
         </div>
         <div class="buy" @click="addFirst" v-show="!food.count || food.count===0">加入购物车</div>
       </div>
       <split></split>
       <div class="info" v-show="food.info">
         <h1 class="title">商品信息</h1>
         <p class="text">{{food.info}}</p>
       </div>
       <split></split>
       <div class="rating">
         <h1 class="title">商品评价</h1>
         <ratingselect @select="selectRating" @toggle="toggleContent" :selectType="selectType" :desc="desc" :ratings="food.ratings" :onlyContent="onlyContent"></ratingselect>
         <div class="rating-wrapper">
           <!--根据ratings长度显示ratings-->
           <ul v-show="food.ratings && food.ratings.length">
             <!--根据不同类型的rateType来切换不同类型的rate-->
             <li  v-for="rating in food.ratings" class="rating-item border-1px" v-show="needShow(rating.rateType,rating.text)">
               <div class="user">
                 <span class="name">{{rating.username}}</span>
                 <img class="avatar" width="12" height="12" :src="rating.avatar">
               </div>
               <!--使用vue过滤器filter来处理时间-->
               <div class="time">{{rating.rateTime |formatDate}}</div>
               <p class="text">
                 <!--根据不同类型的rateType来控制icon的显示-->
                 <span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>
                 {{rating.text}}
               </p>
             </li>
           </ul>
           <!--没有rate的时候显示-->
           <div class="no-rating" v-show="!food.ratings || !food.ratings.length">
             暂无此类评价
           </div>
         </div>
       </div>
     </div>
  </div>
  </transition>
</template>

<script>
  /* eslint-disable arrow-spacing,no-new,comma-spacing,keyword-spacing,padded-blocks */
  import Vue from 'vue'
  import BScroll from 'better-scroll'
  import split from '../split/split.vue'
  import cartcontrol from '../cartcontrol/cartcontrol.vue'
  import ratingselect from '../ratingselect/ratingselect.vue'
  import { formatDate } from '../../common/js/date'
  /* eslint-disable no-trailing-spaces,key-spacing,space-before-function-paren,space-before-blocks,space-infix-ops */
  const ALL = 2
//  const POSITIVE = 0
//  const NEGATIVE = 1
  export default{
   props:{
     food:{
       type:Object
     }
   },
    data(){
      return {
        showFlag:false,
        selectType:ALL,
        onlyContent:true,
        desc:{
          all:'全部',
          positive:'推荐',
          negative:'吐槽'
        }
      }
    },
    filters:{
      formatDate(time){
        let date = new Date(time)
        // 调用date模块的formatDate函数来解析时间
        return formatDate(date, 'yyyy-MM-dd hh:mm')
      }
    },
    methods:{
      show(){
        this.showFlag=true
        this.selectType= 2
        this.onlyContent = true
        this.$nextTick(() => {
          if (!this.scroll) {
            this.scroll = new BScroll(this.$refs.food, {
              click: true
            })
          } else {
            this.scroll.refresh()
          }
        })
      },
      hide(event){
        this.showFlag=false
      },
      addFirst(event){
        console.log('hello')
        if(!event._constructed){
          return
        }
        Vue.set(this.food,'count',1)
      },
      selectRating(type){
        this.selectType= type
        this.$nextTick(()=>{
          this.scroll.refresh()
        })
      },
      toggleContent(onlyContent){
        this.onlyContent= !this.onlyContent
        this.$nextTick(()=>{
          this.scroll.refresh()
        })
      },
      needShow(type,text){
        if(this.onlyContent && !text){
          return false
        }
        if(this.selectType===ALL){
          return true
        }else{
          return type ===this.selectType
        }
  }
    },
    components:{
      cartcontrol,
      split,
      ratingselect
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/style/mixin.styl"
  .food
    display :flex
    position:fixed
    top:0
    left:0
    bottom: 48px
    z-index:30
    width:100%
    background: #ffffff
    transform: translate3d(0, 0, 0)
    &.move-enter-active, &.move-leave-active
      transition: all 0.2s linear
    &.move-enter, &.move-leave-active
      transform: translate3d(100%, 0, 0)
    .food-content
      position:absolute
      top:0
      left:0
      height:auto
      width:100%
      .image-header
        position: relative
        width: 100%
        height: 0
        padding-top: 100%
        img
          position: absolute
          top:0
          left: 0
          width: 100%
          height: 100%
        .back
          position :absolute
          top: 10px
          left: 0
          .icon-arrow_lift
            display: block
            padding: 10px
            font-size: 20px
            color:rgba(255,255,255,0.5)

      .content
        position: relative
        padding: 18px
        .title
          line-height: 14px
          margin-bottom: 8px
          font-size: 14px
          font-weight: 700
          color: rgb(7, 17, 27)
        .detail
          margin-bottom: 18px
          line-height: 10px
          height: 10px
          font-size: 0
        .sell-count, .rating
          font-size: 10px
          color: rgb(147, 153, 159)
        .sell-count
          margin-right: 12px
        .price
          font-weight: 700
          line-height: 24px
          .now
            margin-right: 8px
            font-size: 14px
            color: rgb(240, 20, 20)
          .old
            text-decoration: line-through
            font-size: 10px
            color: rgb(147, 153, 159)

      .cartcontrol-wrapper
          position: absolute
          right: 12px
          bottom: 12px
      .buy
          position: absolute
          right: 18px
          bottom: 18px
          z-index: 10
          height: 24px
          line-height: 24px
          padding: 0 12px
          box-sizing: border-box
          border-radius: 12px
          font-size: 10px
          color: #fff
          background: rgb(0, 160, 220)
          opacity: 1
      .info
        padding: 18px
      .title
        line-height: 14px
        margin-bottom: 6px
        font-size: 14px
        color: rgb(7, 17, 27)
      .text
        line-height: 24px
        padding: 0 8px
        font-size: 12px
        color: rgb(77, 85, 93)
      .rating
        padding-top: 18px
        .title
          line-height: 14px
          margin-left: 18px
          font-size: 14px
          color: rgb(7, 17, 27)
        .rating-wrapper
          padding: 0 18px
          .rating-item
            position: relative
            padding: 16px 0
            border-1px(rgba(7, 17, 27, 0.1))
            .user
              position: absolute
              right: 0
              top: 16px
              line-height: 12px
              font-size: 0
              .name
                display: inline-block
                margin-right: 6px
                vertical-align: top
                font-size: 10px
                color: rgb(147, 153, 159)
              .avatar
                border-radius: 50%
            .time
              margin-bottom: 6px
              line-height: 12px
              font-size: 10px
              color: rgb(147, 153, 159)
            .text
              line-height: 16px
              font-size: 12px
              color: rgb(7, 17, 27)
              .icon-thumb_up, .icon-thumb_down
                margin-right: 4px
                line-height: 16px
                font-size: 12px
              .icon-thumb_up
                color: rgb(0, 160, 220)
              .icon-thumb_down
                color: rgb(147, 153, 159)
          .no-rating
            padding: 16px 0
            font-size: 12px
            color: rgb(147, 153, 159)
</style>




